import object from './object'
import send from './send'

let trackConfig = {
  url:"", //
  channel:"", //
  whiteList:[],// 
} 
export function trackConfig (trackBaseConfig) {
  trackConfig = trackBaseConfig
} 
/**
 * Action
 */
export function trackAction(evt, page, clickOperate = '', addtional = {},uid = "") {
  console.log(trackConfig,"trackConfig")
  if(localStorage.getItem("guid")==null){ //
    localStorage.setItem("guid",object.guid())
  }
  let data = {
    action:"",  //  clickview | pageview
    channel:trackConfig.channel, //
    page,   //
    clickOperate, //  ||||||    actionclickview,clickOperate
    params:{...addtional},  //
    ts: new Date().getTime(),  //
    uuid:localStorage.getItem("guid"), //
    uid:uid, //id
  };

  if (evt === "1" || evt === "4") {
    data.action = "pageView"
    if(localStorage.getItem("sessionTime")==null){ //session
      localStorage.setItem("sessionTime",new Date().getTime())
      localStorage.setItem("sessionId",object.sessionid())
    }else{
      const oldTime = localStorage.getItem("sessionTime")
      const newTime = new Date().getTime()
      const minute =  parseInt(Math.abs(newTime-oldTime)/1000/60);
      if(minute > 10){  //10
        localStorage.setItem("sessionTime",new Date().getTime())
        localStorage.setItem("sessionId",object.sessionid())
      }
    }
    // console.log("UVPV");
  }
  if (evt === "2") {
    // console.log("");
  }
  if (evt === "3") {
    data.action = "clickView"
    // console.log("");
  }
  data.sessionId = localStorage.getItem("sessionId")
  if(trackConfig.whiteList.indexOf(data.page) == -1 || evt == "4"){
    send(trackConfig.url + "?" + object.toQueryString(data)) //
  }
}
console.log(trackAction)


